import {
	GetVodList
} from '@/api/course'
import {
	GetDataList
} from '@/api/class-center'
import {
	KsCourseList
} from 'components/ks-components/course-list/course-list'
import {
	GET
} from 'api/main.entrance.js'
import {
	GetHome
} from 'api/index'
import {
	UniRate
} from 'components/uni/uni-rate/uni-rate'
export default {
	components: {
		KsCourseList,
		UniRate
	},
	data() {
		return {
			courseList: [],
			bannerList: [],
			moduleList: [],
			newsList: [],
			key: '',
			state: {},
			isApp: false,
			appInitPic: false,
			baseUrl: this.$store.state.SRC + "/config/app",
			configData: {},
			tabs: ['精选好课', '新课推荐', '免费好课'],
			clist: [],
			index: 0,
			loaded:false,
			pageCount:1
		}
	},
	onShow() {

	},
	onLoad() {
		
		
		//首冠定制导航
		GET('/config/app/nav.txt').then(res => {
			this.configData = res.data
		
			if (!uni.getStorageSync('appInitPic')) {
				this.appInitPic = true
			}
			this.$store.ready(state => {
				this.state = state
				if(!state.isshowprice){
					uni.setTabBarItem({
					  index: 2,
					  text: '专栏',
					  iconPath: '/static/tabbar/msn.png',
					  selectedIconPath: '/static/tabbar/mss.png'
					})
				}
				this.initData()
			})
		})


	},
	onNavigationBarSearchInputConfirmed(query) {
		uni.navigateTo({
			url: '/pages/course/search?key=' + query.text
		})
		this.key = ''
	},
	onPullDownRefresh() {
		this.initData()
	},
	onShareAppMessage() {
		var that = this
		// 设置菜单中的转发按钮触发转发事件时的转发内容
		var shareObj = {
			title: this.state.siteName,
			path: '/pages/index/index',
			imageUrl: '',
			success: function(res) {
				// 转发成功之后的回调
				if (res.errMsg == 'shareAppMessage:ok') {}
			},
			fail: function() {
				// 转发失败之后的回调
				if (res.errMsg == 'shareAppMessage:fail cancel') {
					// 用户取消转发
				} else if (res.errMsg == 'shareAppMessage:fail') {
					// 转发失败，其中 detail message 为详细失败信息
				}
			}
		};
		// 返回shareObj
		return shareObj;
	},
	methods: {

		getList(i = 0) {
			this.index = i
			this.clist = []
			GetVodList({
				page: 1,
				pagesize: 6,
				popular: i == 0 ? 1 : '',
				recommend: i == 1 ? 1 : '',
				isfree: i == 2 ? 1 : '',
			}).then(res => {
				this.pageCount = res.pageCount
				if (res.result) {
					this.clist = res.data
				}
			})
		},
		openIndex(index) {
			// console.log(index)
		},
		initData() {
			this.$store.ready(state => {
				
				uni.showLoading({
					title: '加载中'
				});
				this.state = state
				GetHome().then(res => {
					
					this.bannerList = res.chart

					this.moduleList = this.configData.list

					//把专栏课程放到底部
					var obj
					this.courseList = res.showinfo.filter(item => {
						if (item.title != '专栏') {
							return item
						} else {
							obj = item
						}
					})

					this.courseList.push(obj)
					//获取课程数据
					this.getList()
					// 获取新闻列表
					GetDataList({
						a: 'getplaytime',
						playtime: 'information', //小组‘group’，问答‘ask’，作品‘works’，资讯‘information’
						pageindex: 1,
						pagesize: 5,
						key: '',
						infoclassid: 0, //资讯的分类id
						orderid: 0 //资讯的筛选类型id
					}).then(res => {
						if (res.result) {
							this.newsList = res.informationmodel.data
						}
					})
					this.$nextTick(() => {
						uni.hideLoading()
						this.loaded = true
						uni.stopPullDownRefresh();
					})
				})

			})

		},
		openTestPage() {
			uni.navigateTo({
				url: '/pages/order-pay/order-pay'
			})
		},
		scroll: function(e) {
			// console.log(e)
		},
		search() {
			if (this.key) {
				var title = this.key
				this.key = ''
				uni.navigateTo({
					url: '/pages/course/search?key=' + title
				})
			}
		},
		open(item){
			if(this.state.isshowprice){
				var url = item.getViewUrl
				url = encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/web/web?title='+item.courseName+'&photo='+item.courseImg+'&url='+url
				})
			}
				
			
		},
		openNews(item){
			if(item.ischarge){
				uni.showToast({
				    title: '暂不支持观看付费资讯，请到h5或pc端查看',
				    duration: 2000,
					icon:'none',
				});
			}else{
				var url = encodeURIComponent(item.weburl)
				uni.navigateTo({
					url:'/pages/web/web?title='+item.title+'&photo='+item.defaultpic+'&url='+url
				})
			}
		},
		openNav(item) {

			if (item.id.length == 1) {
				uni.navigateTo({
					url: '/pages/course/courselist?type=1&yjId=' + item.id[0]
				})
			} else if (item.id.length == 2) {
				uni.navigateTo({
					url: '/pages/course/courselist?type=1&yjId=' + item.id[0] + '&ejId=' + item.id[1]
				})
			} else {
				uni.navigateTo({
					url: '/pages/course/courselist?type=1&yjId=' + item.id[0] + '&ejId=' + item.id[1] + '&sjId=' + item.id[2]
				})
			}

		},
		openView(url) {
			uni.navigateTo({
				url: url
			});
		}
	}
}
